@font-face {
  font-weight: 400;
  font-family: "HuaweiSans";
  font-style: normal;
  src: url("../fonts/HuaweiSans.woff2") format("woff2"), url("../fonts/HuaweiSans.woff") format("woff");
  font-display: swap; }

@-webkit-keyframes scale {
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes scale {
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  font-family: "HuaweiSans", "Arial", sans-serif;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.button {
  font-size: 25px;
  background-color: #c30c15;
  color: #ffffff;
  padding: 11px 20px;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  text-align: center;
  width: 211px;
  -webkit-animation: scale 3s linear infinite;
          animation: scale 3s linear infinite; }

.container {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto; }

.left-banner {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: calc((100% - 960px) / 2); }

.content-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  max-width: 500px;
  max-height: 1000px; }

.logo {
  position: absolute;
  background-image: url("../img/logo.svg");
  background-repeat: no-repeat;
  background-position: 50% 100%;
  background-size: contain;
  width: 76px;
  height: 77px;
  top: 30px;
  right: 30px; }

.right-headphone {
  position: absolute;
  background: url("../img/right-headphones.png");
  background-size: cover;
  background-repeat: no-repeat;
  width: 138px;
  height: 185px;
  top: 144px;
  right: 43px; }

.left-headphone {
  position: absolute;
  background: url("../img/left-headphones.png");
  background-repeat: no-repeat;
  width: 112px;
  height: 177px;
  top: 340px;
  right: 153px; }

.active-noise {
  position: absolute;
  background-image: url("../img/active-noise.svg");
  background-repeat: no-repeat;
  width: 138px;
  height: 44px;
  margin-left: auto;
  right: 5px;
  bottom: 350px; }

.headphones-case1 {
  position: absolute;
  background-image: url("../img/headphones-case.png");
  background-repeat: no-repeat;
  width: 369px;
  height: 386px;
  bottom: 7px;
  right: 49px; }

@media (max-width: 1900px) {
  .right-headphone {
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }
  .left-headphone {
    right: 120px;
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }
  .headphones-case1 {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    bottom: 0; }
  .active-noise {
    bottom: 310px; } }

@media (max-width: 1770px) {
  .right-headphone {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    top: 104px;
    right: 13px; }
  .left-headphone {
    top: 250px;
    right: 90px;
    -webkit-transform: scale(0.7);
            transform: scale(0.7); }
  .headphones-case1 {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    bottom: -40px;
    right: -50px; }
  .active-noise {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    bottom: 260px; } }

.top-banner {
  margin: 0 auto;
  width: 960px;
  height: 250px; }

.label {
  background-image: url("../img/label.svg");
  background-repeat: no-repeat;
  background-position: 50% 100%;
  background-size: contain;
  width: 516px;
  height: 38px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 100px;
  margin-bottom: 10px; }

.promo {
  margin: 0;
  font-size: 29px;
  text-transform: uppercase;
  line-height: 1.2;
  text-align: center;
  margin-bottom: 25px; }

.right-banner {
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  width: calc((100% - 960px) / 2); }

.headphones-case2 {
  position: absolute;
  background-image: url("../img/headphones-case2.png");
  background-repeat: no-repeat;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 425px;
  height: 384px; }

.right-banner-wrapper {
  position: absolute;
  width: 350px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 45px; }

@media (max-width: 1900px) {
  .headphones-case2 {
    -webkit-transform: translate(-50%, -50%) scale(0.8);
            transform: translate(-50%, -50%) scale(0.8); } }

@media (max-width: 1770px) {
  .headphones-case2 {
    -webkit-transform: translate(-50%, -50%) scale(0.7);
            transform: translate(-50%, -50%) scale(0.7); }
  .right-banner-wrapper {
    -webkit-transform: translateX(-50%) scale(0.8);
            transform: translateX(-50%) scale(0.8); } }

@media (max-width: 1460px) {
  .headphones-case2 {
    -webkit-transform: translate(-50%, -50%) scale(0.4);
            transform: translate(-50%, -50%) scale(0.4); }
  .right-banner-wrapper {
    -webkit-transform: translateX(-50%) scale(0.6);
            transform: translateX(-50%) scale(0.6); } }

.content {
  display: block;
  margin-left: auto;
  margin-right: auto; }
